import {
	urlBase64ToUint8Array
} from './utils';
import FingerprintJS from '@fingerprintjs/fingerprintjs';
import {
	subscribe,
	getdeviceGenerate
} from "../api/setting"

export async function subscribeToPush() {
	
	if ('serviceWorker' in navigator && 'PushManager' in window) {
		
		const registration = await navigator.serviceWorker.ready;
		
		// const existingSubscription = await registration.pushManager.getSubscription();
		// if (existingSubscription) {
		// 	return existingSubscription;
		// }
		
		const vapidPublicKey =
			'BA-1y_WvzeQmMrE3ZFxK0HHtOsROS05Xn9KPgZmeZ2tqE-bv9KXS9nTMy4PyXJXYe9hKRPuKguc2Qte98vp9I8E';
		const applicationServerKey = urlBase64ToUint8Array(vapidPublicKey);
		
		const newSubscription = await registration.pushManager.subscribe({
			userVisibleOnly: true,
			applicationServerKey
		});
		
		let visitorIdStr = localStorage.getItem("visitorId")
		if (visitorIdStr === 'null' || !visitorIdStr) {
			try {
				const fp = await FingerprintJS.load();
				const result = await fp.get()
				const data = {
					fingerprint: result.visitorId
				}
				getdeviceGenerate(data).then(res => {
					if (res.meta.code == 0) {
						localStorage.setItem("visitorId", res.data.device_id)
						let obj = {
							subscription: newSubscription ? newSubscription.toJSON() : {},
							pixel_id: null,
							device_id: res.data.device_id
						}
						subscribe(obj).then(res => {
							if (res.meta.code == 0) {
								// 成功处理逻辑占位
							}
						})
					}
				})
			} catch (error) {
				// 忽略错误
			}
		} else {
			let obj = {
				subscription: newSubscription ? newSubscription.toJSON() : {},
				pixel_id: null,
				device_id: visitorIdStr
			}
			subscribe(obj).then(res => {
				if (res.meta.code == 0) {
					// 成功处理逻辑占位
				}
			})
		}


		return newSubscription;
	}
}