<template>
	<div class="home-mobile">
		<div class="home" v-if="lodingShow"><img class="container bottom-layout" src="../assets/bottom.png" alt=""
				@click="dowload"><img class="container head-layout" src="../assets/google.png" alt="">
			<div class="head-div"></div><img class="container title-layout" :src="setting.header_img" alt="">
			<div class="container download-layout"><img class="download-btn" src="../assets/btn.gif" @click="dowload">
			</div><img class="container share-layout" src="../assets/share.png" alt="">
			<div class="container adv-layout">
				<div class="horizontal-container"><img class="adv-img" v-for="(item,i) in setting.banner_img" :key="i"
						:src="item">
				</div>
			</div><img class="container content-layout" :src="setting.pwa_comment_img" alt="">
			<div class="bottom-div"></div>
		</div>
		<div v-if="downloadShow">
			<div class="modal-overlay"></div>
			<div class="modal" role="dialog">
				<div class="model-body" style="color: white;">
					<div class="top"></div>
					<div class="content">Rapid Instalar</div>
					<div class="effective-layout"><img class="safe-img" src="../assets/down.png"><span>Effective</span>
					</div>
					<div class="install-layout">
						<div class="install-btn" style="display: none;">aberto</div>
						<div class="install-btn" @click="installApp()">Instalar</div>
						<div class="progress-layout" style="display: none;">
							<div class="progress-container">
								<div class="progress-bar" style="width: calc(0vw);"></div>
								<div class="progress-txt">0%</div>
							</div>
						</div>
					</div>
				</div><img class="down-img" src="../assets/downIcon.png">
			</div>
		</div>
		<div v-if="iosDownloadShow">
			<div class="modal-overlay"></div>
			<div class="modal" role="dialog"><img class="ios_bg" src="../assets/iosDown.png" alt="">
				<div class="close_btn" @click="iosDownloadShow = false"></div>
			</div>
		</div>
		<div class="loading" v-if="!lodingShow"><span></span><span></span><span></span><span></span><span></span></div>
	</div>
</template>

<script>
	import {
		getSetting
	} from "../api/setting"

	export default {
		data() {
			return {
				lodingShow: false,
				downloadShow: false,
				iosDownloadShow: false,
				installPrompt: null,
				setting: {
					installation_address: "",
					header_img: "",
					banner_img: [],
					pwa_comment_img: ""
				}
			}
		},
		mounted() {
			this.infoPage()
			window.addEventListener('beforeinstallprompt', this.handleInstallPrompt);
			window.addEventListener('appinstalled', this.handleAppInstalled);
		},
		beforeDestroy() {
			window.removeEventListener('beforeinstallprompt', this.handleInstallPrompt)
			window.removeEventListener('appinstalled', this.handleAppInstalled)
		},
		methods: {
			infoPage() {
				getSetting().then(res => {
					if (res.meta.code === 0) {
						this.setting = res.data
						this.toHome()
						setTimeout(() => {
							this.lodingShow = true
						}, 1000)
					}
				}).catch(()=>{
					this.toHome()
					setTimeout(() => {
						this.lodingShow = true
					}, 1000)
				})
			},
			toHome() {
				const isInStandaloneMode = () => {
					// 标准PWA检测
					if (window.matchMedia('(display-mode: standalone)').matches) {
						return true;
					}

					// iOS主屏幕检测
					if (window.navigator.standalone === true) {
						return true;
					}

					// 华为书签模式检测
					const huaweiPattern = /HuaweiBrowser|HBBrowser/i;
					const outerHeight = localStorage.getItem("outerHeight")
					const condition1 = huaweiPattern.test(navigator.userAgent); // 桌面模式特性
					const condition2 = window.outerHeight === window.innerHeight; // 无浏览器镶边
					const condition3 = window.navigator.standalone === undefined;
					const condition4 = outerHeight < window.outerHeight;
					const truthyCount = [condition1, condition2, condition3, condition4].filter(Boolean).length;
					if (truthyCount >= 4) return true;

					// 尺寸检测（适用于其他设备）
					// const screenDiff = window.outerHeight - window.innerHeight;
					// if (screenDiff <= 60 && screenDiff > 0) {
					// 	return true;
					// }

					return false;
				};

				if (isInStandaloneMode()) {
					console.log('当前运行在 PWA 独立模式');
					if (this.setting.installation_address) {
						window.location.href = this.setting.installation_address
					}
				} else {
					console.log('当前运行在浏览器中');
				}
			},
			async installApp() {
				if (this.installPrompt) {
					// 触发安装提示
					this.installPrompt.prompt();
					// 等待用户选择结果
					const result = await this.installPrompt.userChoice;
					// 根据结果进行相应处理
					if (result.outcome === 'accepted') {
						console.log('用户接受了安装');
						localStorage.setItem("outerHeight", window.outerHeight)
						window.location.href = this.setting.installation_address
					} else {
						console.log('用户拒绝了安装');
					}
					// 重置安装提示
					this.installPrompt = null;
				} else {
					window.location.href = this.setting.installation_address
				}
			},
			dowload() {
				if (this.isIOS()) {
					this.iosDownloadShow = true
				} else {
					this.downloadShow = true
				}
			},
			isIOS() {
				const ua = navigator.userAgent;
				return /iPad|iPhone|iPod/.test(ua) && !window.MSStream;
			},
			handleInstallPrompt(e) {
				e.preventDefault()
				this.installPrompt = e
			},
			handleAppInstalled() {
				this.installPrompt = null
			}
		}
	}
</script>
