import request from '@/utils/request'

export function getSetting(params) {
	return request({
		url: '/api/pwa_config/getsetting',
		method: 'get',
		params
	})
}

export function subscribe(data) {
	return request({
		url: '/api/webpush/subscribe',
		method: 'post',
		data
	})
}

export function getdeviceGenerate(data) {
	return request({
		url: '/api/login_device/generate',
		method: 'post',
		data
	})
}